@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Josefin Sans';
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body > #root > div {
  height: 100vh;
}
.container {
    width: 50%;
    margin: 0 auto;
    color: white;
}

.header {
    padding-top: 50px;
    margin-top: 0;
}

.add-todo {
    width: 100%;
}

.list {
    margin-top: 24px;
}

@media only screen and (max-width: 700px) {
    .container {
        width: 80%;
    }
}

.footer {
    height: 32px;
    background-color:#777a92;
    color: #25273c;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 8px 6px 8px;
}

.link:hover {
    color: white;
}

.small-footer {
    display: none;
}

@media only screen and (max-width: 700px) {
    .footer {
        display: none;
    }

    .small-footer {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .todo-info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 8px;
        height: 40px;
        background-color:#777a92;
        color: #25273c;
    }

    .filter {
        text-align: center;
        margin-top: 16px;
        height: 40px;
        background-color:#777a92;
        color: #25273c;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
.app {
    background-color: hsl(235, 24%, 19%);
}
